body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-image: linear-gradient(to bottom, #e0c56b, #2e2e2e);
  height: 100vh
}

.title {
  color: white;
  text-shadow: 0px 4px 15px black;
}

@font-face {
  font-family: 'power_symbols';
  src: url(/lampcontrol/static/media/Unicode_IEC_symbol.3e827d7e.woff2) format('woff2'), url(/lampcontrol/static/media/Unicode_IEC_symbol.46dafe8b.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

.power {
  font-family: 'power_symbols';
  font-style: normal;
  font-size: 1em;
}
